@font-face {
  font-family: 'Fontin';
  src: url('../../fonts/Fontin.ttf') format('truetype');
}

.leftPanel {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 22%;
  height: 100%;
  border: 1px solid white;
}

.menuButton {
  border: 0px;
  margin: auto;
  padding-left: 2vw;
  width: 100%;
  height: 20%;
  background: linear-gradient(
    90deg,
    rgba(27, 9, 2, 1) 5%,
    rgba(160, 27, 8, 1) 50%,
    rgba(27, 9, 2, 1) 95%
  );
  color: #e8b656;
  font-size: 1.4vw;
  font-family: Fontin;
  font-weight: bold;
  text-shadow: 0px 0px 40px white;
  text-align: left;
  transition-duration: 0.4s;
}

.menuButton:hover {
  padding-left: 7vw;
}
